
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import scroll from '@/components/scroll/scroll'
import slide from '@/components/Slide/slide'
export default{
components: {
XHeader,Tab,TabItem,Swiper, SwiperItem,scroll,slide
},
data() {
return {
tabIndex: 0,
noData: [false, false, false],
start: "",
num: ["1", "12", "1200"],
pageIndex: [1, 1, 1],
pageSize: 15,
listMonth: [],
listYear: [],
listAll: [],
}
},
watch: {
tabIndex: function(newPage,oldPage){
console.log(newPage);
this.$refs.slide.goToPage(newPage);
switch (this.tabIndex) {
case 0:
if (this.listMonth.length <= 0) {
this.getSurgicalList(true);
}
break;
case 1:
if (this.listYear.length <= 0) {
this.getSurgicalList(true);
}
break;
case 2:
if (this.listAll.length <= 0) {
this.getSurgicalList(true);
}
break;
default:
break;
}
},
},
mounted() {
this.getCurrentDate();
this.getSurgicalList(true);
},
methods: {
slide(index){
},

getCurrentDate() {
var date = new Date();
var seperator = "";
var year = date.getFullYear();
var month = date.getMonth() + 1;
if (month >= 1 && month <= 9) {
month = "0" + month;
}
this.start = year + seperator + month;
},

getSurgicalList(upOrDown) {
if (upOrDown) {
this.pageIndex[this.tabIndex] = 1;
}
this.$vux.loading.show({
text: '加载中...'
});
this.$ajax.post('',{
"MSH.1": "operationLog",
"MSH.2": "list",
"CHZBM": this.$root.loginInfo.CHZBM,
"start": this.start,
"num": this.num[this.tabIndex],
"pageIndex": this.pageIndex[this.tabIndex],
"pageSize": this.pageSize,
'token':this.$root.loginInfo.token,
}).then(res => {
console.log(res);
this.$vux.loading.hide();
if(typeof res != 'undefined' && typeof res.rows != 'undefined') {
if (res.pageNumber == 1) {
this.pageIndex[this.tabIndex] = 1;
switch (this.tabIndex) {
case 0:
this.listMonth = [];
break;
case 1:
this.listYear = [];
break;
case 2:
this.listAll = [];
break;
default:
break;
}
}
if (res.rows.length > 0) {
this.noData[this.tabIndex] = false;
this.pageIndex[this.tabIndex]++;
switch (this.tabIndex) {
case 0:
this.listMonth = [...this.listMonth,...res.rows];
break;
case 1:
this.listYear = [...this.listYear,...res.rows];
break;
case 2:
this.listAll = [...this.listAll,...res.rows];
break;
default:
break;
}
} else {
if (res.pageNumber > 1) {
this.$vux.toast.text('没有更多了', 'bottom');
} else {
this.noData[this.tabIndex] = true;
}
}
}
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
})
},
}
}



